<template>
  <div class="shopbag">
    <div class="topBgcolor"></div>

    <!-- 导航条 -->
    <van-nav-bar
      title="购物车"
      left-text="返回"
      left-arrow
      :fixed='true'
      @click-right="editProduct()"
      @click-left="back()"
    >
      <!-- 无商品导航 -->
      <template
        #right
        v-if="shopbagData.length > 0"
      >
        <div>{{isEdit ? '完成' : '编辑'}}</div>
      </template>
    </van-nav-bar>

    <!-- 头部背景 -->
    <div class="shopbag-bg"></div>

    <!-- 商品列表 -->
    <div
      class="main-box"
      v-if="shopbagData.length > 0"
    >
      <van-swipe-cell
        :disabled="!isEdit"
        v-for="(item,index) in shopbagData"
        :key="index"
      >
        <van-cell :border="false">
          <!-- 使用 title 插槽来自定义标题 -->
          <template #title>
            <div class="clearfix cell-box">
              <div class="fl check">
                <van-checkbox
                  v-model="item.isCheck"
                  checked-color="#0b34ba"
                  icon-size="22px"
                  @change="simpleSelect"
                ></van-checkbox>
              </div>
              <div class="fl pro-img" @click="viewProductInfo(item.pid)">
                <img
                  class="auto-img"
                  :src="item.small_img"
                  alt=""
                >
              </div>
              <div class="fl pro-info">
                <div class="text-box">
                  <div class="clearfix text-box-item">
                    <div class="fl text-name">{{item.name}}</div>
                    <div class="fl text-rule">{{item.rule}}</div>
                  </div>
                  <div class="text-enname">{{item.enname}}</div>
                </div>
                <div class="price-box">
                  <div class="fl price">￥{{item.price}}</div>
                  <div class="fr">
                    <van-stepper
                      v-model="item.count"
                      theme="round"
                      button-size="22"
                      disable-input
                      @change="modifyCount(item)"
                    />
                  </div>
                </div>
              </div>
            </div>
          </template>
        </van-cell>
        <template #right>
          <van-button
            square
            color="#0b34ba"
            text="删除"
            @click="removeOneShopbag(item.sid,index)"
          />
        </template>
      </van-swipe-cell>

      <!-- 底部菜单 -->
      <div>
        <van-submit-bar
          v-show="!isEdit"
          :price="total"
          button-color="#0b34ba"
          button-text="提交订单"
          @submit="commit"
        >
          <van-checkbox
            v-model="allCheck"
            icon-size="22px"
            checked-color="#0b34ba"
            @click="allSelect"
          >全选</van-checkbox>
        </van-submit-bar>

        <van-submit-bar
          v-show="isEdit"
          button-color="#0b34ba"
          button-text="删除选择"
          @submit="removeMoreShopbag"
        >
          <van-checkbox
            v-model="allCheck"
            icon-size="22px"
            checked-color="#0b34ba"
            @click="allSelect"
          >全选</van-checkbox>
        </van-submit-bar>
      </div>
    </div>

    <!-- 无商品的列表背景 -->
    <div
      class="main-box-null"
      v-else
    >
      <van-empty
        class="custom-image"
        image="https://img.yzcdn.cn/vant/custom-empty-image.png"
        description="没有东西啦，快去选购吧"
      >
        <van-button
          round
          color="#0b34ba"
          class="bottom-button"
          @click="goMenu"
        >
          马上去剁手
        </van-button>
      </van-empty>
    </div>

  </div>

</template>

<script>
import "../../assets/less/shopbag.css";
import { utils } from "../../assets/js/utils";

export default {
  data() {
    return {
      isEdit: false,
      allCheck: false,
      shopbagData: [],
      total: 0,
    };
  },
  created() {
    this.findShopbag();
  },
  methods: {
    // 回到上一步
    back() {
      this.$router.go(-1);
    },

    // 回到菜单页面
    goMenu() {
      this.$router.push({ name: "Menu" });
    },

    // 提交订单
    commit() {
      // 查找需要购买的商品,查sid
      let sids = [];
      this.shopbagData.map((v) => {
        if (v.isCheck) {
          sids.push(v.sid);
        }
      });

      if (sids.length === 0) {
        this.$toast("请选择购买商品");
        return;
      }

      sids = sids.join("-");
      this.$router.push({ name: "Pay", query: { sids } });
      
    },

    // 编辑商品
    editProduct() {
      this.isEdit = !this.isEdit;
    },

    // 查询购物车数目
    findShopbag() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code == 5000) {
            result.data.result.map((v) => {
              v.isCheck = false;
            });

            this.shopbagData = result.data.result;
            
          } else {
            this.$toast("亲~ 请先登录");
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 查看商品详情
    viewProductInfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },

    // 全选
    allSelect() {
      this.shopbagData.map((v) => {
        v.isCheck = this.allCheck;
      });
      this.sum();
    },

    // 单选
    simpleSelect() {
      this.sum();
      for (let i = 0; i < this.shopbagData.length; i++) {
        if (!this.shopbagData[i].isCheck) {
          this.allCheck = false;
          return;
        }
      }
      this.allCheck = true;
    },

    // 修改商品数量
    modifyCount(item) {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      // 序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sid: item.sid,
        count: item.count,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        data,
      })
        .then((result) => {
          this.$toast.clear();
          // 
          this.sum();
          if (result.data.code !== 6000) {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 删除购物袋数据
    removeShopbag(sids) {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      // sids:sid的集合，类型：array

      // 序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        // 转成集合
        sids: JSON.stringify(sids),
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      return this.axios({
        method: "POST",
        url: "/deleteShopcart",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        data,
      });
    },

    // 删除单个商品
    removeOneShopbag(sid, index) {
      this.removeShopbag([sid])
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 7000) {
            this.shopbagData.splice(index, 1);
          }
          this.$toast(result.data.msg);
          this.sum();
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 删除多个商品
    removeMoreShopbag() {
      // 查找选择的商品
      let sids = [];
      this.shopbagData.map((v) => {
        if (v.isCheck) {
          sids.push(v.sid);
        }
      });

      this.removeShopbag(sids)
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 7000) {
            for (let i = this.shopbagData.length - 1; i >= 0; i--) {
              if (this.shopbagData[i].isCheck) {
                this.shopbagData.splice(i, 1);
              }
            }
            this.sum();
          }
          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 计算总金额
    sum() {
      this.total = 0;
      this.shopbagData.map((v) => {
        if (v.isCheck) {
          this.total += v.count * Number(v.price);
        }
      });
      // 以分为单位
      this.total *= 100;
    },
  },
};
</script>
